import React from "react";
import * as Semantic from "semantic-ui-react";
import * as ReactLogo from "../../assets/react.png";
import * as AngularLogo from "../../assets/angular.png";
import * as PhpLogo from "../../assets/php.png";
import * as MySqlLogo from "../../assets/mysql.png";
import * as ApacheLogo from "../../assets/apache.png";
import BasePolarChart from "../../component/base/base-polar-chart";
import CardFeedItem from './../../component/card-feed-item';

const Skill = () => {
  const skillData = [
    { name: "Angular", value: 5, logo: AngularLogo },
    { name: "ReactJs", value: 5, logo: ReactLogo },
    { name: "PHP", value: 8, logo: PhpLogo },
    { name: "API Integration", value: 7 },
    { name: "Apache", value: 7, logo: ApacheLogo},
    { name: "Symfony", value: 8 },
    { name: "Drupal", value: 7 },
    { name: "Cloud Services", value: 5 },
    { name: "Microservices", value: 5},
    { name: "MySql", value: 6, logo: MySqlLogo },
    { name: "Java", value: 5}
  ];

  return (
    <React.Fragment>
      <Semantic.Header as="h2" id="skill-set">
        Technologies
      </Semantic.Header>

      <Semantic.Grid columns={2}>
        <Semantic.Grid.Column>
          <Semantic.Card fluid>
            <Semantic.Card.Content>
              <Semantic.Card.Header>Back End</Semantic.Card.Header>
            </Semantic.Card.Content>

            <Semantic.Card.Content>
              <CardFeedItem image={PhpLogo} date="[Some Date]">
                Php 5.6+
              </CardFeedItem>
            </Semantic.Card.Content>

            <Semantic.Card.Content>
              <CardFeedItem image={ApacheLogo} date="[Some Date]">
                Apache
              </CardFeedItem>
            </Semantic.Card.Content>

            <Semantic.Card.Content>
              <CardFeedItem image={MySqlLogo} date="[Some Date]">
                MySql
              </CardFeedItem>
            </Semantic.Card.Content>
          </Semantic.Card>
        </Semantic.Grid.Column>

        <Semantic.Grid.Column>
          <Semantic.Card fluid>
            <Semantic.Card.Content>
              <Semantic.Card.Header>Front End</Semantic.Card.Header>
            </Semantic.Card.Content>

            <Semantic.Card.Content>
              <CardFeedItem image={ReactLogo} date="[Some Date]">
                React
              </CardFeedItem>
            </Semantic.Card.Content>

            <Semantic.Card.Content>
              <CardFeedItem image={AngularLogo} date="[Some Date]">
                Angular
              </CardFeedItem>
            </Semantic.Card.Content>
          </Semantic.Card>
        </Semantic.Grid.Column>
      </Semantic.Grid>

      <Semantic.Grid>
        <BasePolarChart data={skillData}></BasePolarChart>
      </Semantic.Grid>
    </React.Fragment>
  );
};

export default Skill;
