import React from "react";
import * as Semantic from "semantic-ui-react";
import LayoutHeaderBody from "../layout/layout-header-body";
import * as Portrait from "../../assets/portrait.jpg";
import PlaceholderArticle from "../../component/placeholders/place-holder-article";
import SocialMediaLink from '../../component/social-media-link';

const About = () => {

  const header = (
    <React.Fragment>
      <Semantic.Image src={Portrait} circular size="medium" centered />

      <Semantic.Header as="h2" icon textAlign="center">
        <Semantic.Header.Content>Hi, I am Leonard Lam.</Semantic.Header.Content>
      </Semantic.Header>

      <Semantic.Header as="p" icon textAlign="center">
        <Semantic.Header.Content>
          I develop and personalized technical strategies to meet your requirements.
        </Semantic.Header.Content>
      </Semantic.Header>

      <Semantic.Container textAlign="center">
        <SocialMediaLink href="https://github.com/leo-webstack" label="Github" color="github" icon="github"></SocialMediaLink>
        <SocialMediaLink href="https://linkedin.com/in/leo-webstack" label="LinkedIn" color="linkedin" icon="linkedin"></SocialMediaLink>
      </Semantic.Container>
    </React.Fragment>
  );

  return (
    <React.Fragment>
      <LayoutHeaderBody header={header} className="background-image">
        <PlaceholderArticle></PlaceholderArticle>
      </LayoutHeaderBody>
    </React.Fragment>
  );
};

export default About;
