import React from "react";
import * as Semantic from "semantic-ui-react";

const PlaceholderArticle = () => {
  const content = [];

  for (let i = 0; i < 5; i++) {
    content.push(
      <Semantic.Placeholder.Paragraph key={i}>
        <Semantic.Placeholder.Line />
        <Semantic.Placeholder.Line />
        <Semantic.Placeholder.Line />
      </Semantic.Placeholder.Paragraph>
    );
  }

  return (
    <React.Fragment>
      Sample Article Component
      <Semantic.Header as="h2">[Title]</Semantic.Header>
      <Semantic.Placeholder fluid>
        <Semantic.Placeholder.Header image>
          <Semantic.Placeholder.Line />
          <Semantic.Placeholder.Line />
        </Semantic.Placeholder.Header>
        {content}
      </Semantic.Placeholder>
    </React.Fragment>
  );
};

export default PlaceholderArticle;
