import React from "react";

import {
  VictoryChart,
  VictoryBar,
  VictoryTheme,
  VictoryPolarAxis,
  VictoryStack,
} from "victory";

const BasePolarChart = ({ data }) => {
  const getStyle = (fillColor) => {
    const n = data.length ? data.length : 1;
    return {
      data: {
        fill: fillColor,
        width: 360 / n,
        stroke: "#FFFFFF",
        strokeWidth: 1,
      },
    };
  };

  const style = {
    primary: getStyle("#333"),
    secondary: getStyle("darkOrange"),
  };

  const innerStyle = {
    radius: 20,
  };

  const onHover = () => {
    return [
      {
        childName: "all",
        target: "data",
        eventHandlers: {
          onMouseOver: () => {
            return [
              { target: "labels", mutation: () => ({ active: true }) },
              { target: "data", mutation: () => ({ active: true }) },
            ];
          },
          onMouseOut: () => {
            return [
              { target: "labels", mutation: () => ({ active: false }) },
              { target: "data", mutation: () => ({ active: false }) },
            ];
          },
        },
      },
    ];
  };

  return (
    <VictoryChart
      polar
      theme={VictoryTheme.material}
      domainPadding={{ y: 10 }}
      innerRadius={innerStyle.radius}
      events={onHover}
    >
      <VictoryPolarAxis tickValues={data.map((v) => v.naame)} />

      <VictoryStack
        style={{ data: { width: 50 } }}
      >
        <VictoryBar
          style={style.primary}
          data={data.map((v) => {
            return { x: v.name, y: v.value };
          })}
        />

        <VictoryBar
          style={style.secondary}
          data={data.map((v) => {
            return { x: v.name, y: v.value * 0.2 };
          })}
        />
      </VictoryStack>
    </VictoryChart>
  );
};

export default BasePolarChart;
