import React from "react";
import * as Semantic from "semantic-ui-react";

const CardFeedItem = ({ image, date, children }) => {
  return (
    <Semantic.Feed>
      <Semantic.Feed.Event>
        <Semantic.Feed.Label image={image} />
        <Semantic.Feed.Content>
          <Semantic.Feed.Date content={date} />
          <Semantic.Feed.Summary>{children}</Semantic.Feed.Summary>
        </Semantic.Feed.Content>
      </Semantic.Feed.Event>
    </Semantic.Feed>
  );
};

export default CardFeedItem;
