import React from "react";
import * as Semantic from "semantic-ui-react";

const LayoutHeaderBody = (props) => {
  return (
    <React.Fragment>
      <Semantic.Grid divided="vertically">
        <Semantic.Grid.Row columns={1}>
          <Semantic.Grid.Column>{props.header}</Semantic.Grid.Column>
        </Semantic.Grid.Row>

        <Semantic.Grid.Row columns={1}>
          <Semantic.Grid.Column>{props.children}</Semantic.Grid.Column>
        </Semantic.Grid.Row>
      </Semantic.Grid>
    </React.Fragment>
  );
};

export default LayoutHeaderBody;
